.content {
    width: 75vw;
    margin: auto;
    padding: 10px;
}

.content .heading {
    text-align: center;
    font-size: 3em;
    margin: 10px;
}

.content .subheading {
    text-align: center;
    font-size: 1.25em;
    margin-top: 5vh;
    padding: 1em;
}

.projects {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    z-index: 2;
}